<script>
import anime from 'animejs';
import { mapGetters, mapActions } from 'vuex';
export default {
  name: 'Header',
  data() {
    return {
      theme: '',
      path1: 'M31.2 20C31.2 23.5326 29.5645 26.6832 27.0094 28.736C25.0905 30.2776 22.653 31.2 20 31.2C13.8144 31.2 8.8 26.1856 8.8 20C8.8 13.8144 13.8144 8.79997 20 8.79997C22.653 8.79997 25.0905 9.72238 27.0094 11.264C29.5645 13.3167 31.2 16.4674 31.2 20Z',
      path2: 'M32 4.38454C24.9576 5.98171 19.6938 12.3666 19.6938 20C19.6938 27.6334 24.9576 34.0183 32 35.6155C28.6288 38.359 24.35 40 19.6938 40C8.81724 40 0 31.0457 0 20C0 8.9543 8.81724 0 19.6938 0C24.35 0 28.6288 1.64099 32 4.38454Z',
    }
  },
  computed: {
    ...mapGetters(['getTheme'])
  },
  mounted() {
    this.theme = this.getTheme
    window.addEventListener('storage', e => {
      if (e.key === 'theme') {
        this.animation('other');
      }
    });
  },
  destroyed() {
    window.removeEventListener('storage', e => {
      if (e.key === 'theme') {
        this.animation('other');
      }
    });
  },
  methods: {
    ...mapActions(['setTheme']),
    animation(mode) {
      let path;
      let opacity;

      if (mode) {
        path = this.getTheme !== 'light' ? this.path1 : this.path2;
        opacity = this.getTheme !== 'light' ? 1 : 0;
      } else {
        path = this.getTheme === 'light' ? this.path1 : this.path2;
        opacity = this.getTheme === 'light' ? 1 : 0;
      }

      anime({
        targets: '.btn-core',
        d: [
          {
            value: [
              path
            ]
          },
        ],
        easing: 'easeOutQuad',
        duration: 500,
        autoplay: true
      });
      anime({
        targets: '.btn-rays',
        opacity,
        easing: 'easeOutQuad',
        duration: 500,
        autoplay: true
      });
    },
    changeTheme() {
      this.setTheme();
      this.animation();
    }
  }
}
</script>

<style lang="scss">
  .header {
    grid-area: header;
    background: var(--primary);
    height: 54px;
    display: flex;
    align-items: center;
    transition: background .5s;
    border-bottom: 1px solid var(--border-color);

    > .header__logo {
      margin-left: 5px;
    }

    > .header__item {
      cursor: pointer;
      -webkit-tap-highlight-color: transparent;
      margin-left: auto;
      height: 100%;
      padding: 0 5px;
      display: flex;
      align-items: center;
    }
  }
</style>